import React from 'react';
import {
  A,
  Container,
  Heading,
  Navigation,
  NavItem,
  SubHeader,
  Small,
  Provider,
} from 'pure';
import MediumExample from './MediumExample';

export default class App extends React.Component {
  render() {
    return (
      <Provider>
        <Container style={{maxWidth: 1000}} m={'auto'}>
          <Container textAlignCenter p={100} sm={{ padding: '130px' }}>
            <Heading fontSize={42} fontWeight={500}>Pur</Heading>
            <SubHeader fontWeight={300} mt={30}>Create powerful designs, write less — with React.</SubHeader>
            <Navigation fluid justifyContentCenter fontSize={14}>
              <NavItem active><A href="https://github.com/eveningkid/react-pur">Get Started</A></NavItem>
            </Navigation>
          </Container>

          <MediumExample />

          <Container p={100} textAlignCenter>
            <Heading fontSize={20}><A href="https://github.com/eveningkid/react-pur/blob/master/examples/Medium.jsx" underlined>This responsive Medium-looking example only uses Pur.</A></Heading>
            <Small muted>Brought to you by <A href="https://twitter.com/eveningkid" simple underlined>Arnaud Dellinger</A></Small>
          </Container>
        </Container>
      </Provider>
    );
  }
}



